*,*:before,*:after{
    box-sizing:border-box;
    margin:0;
    padding:0;
    /*transition*/
    -webkit-transition:.25s ease-in-out;
       -moz-transition:.25s ease-in-out;
         -o-transition:.25s ease-in-out;
            transition:.25s ease-in-out;
    outline:none;
    }
  
   
    #toggles{
    width:4rem;
    margin-left: 0.5rem;
  
    text-align:center;
   

    }
    .ios-toggle,.ios-toggle:active{
    position:absolute;
    top:-5000px;
    height:0;
    width:0;
    opacity:0;
    border:none;
    outline:none;
    }
    .checkbox-label{
    display:block;
    position:relative;
    padding:10px;
  
    font-size:12px;
    line-height:16px;
    width:100%;
    height:2rem;
    /*border-radius*/
    -webkit-border-radius:0px;
       -moz-border-radius:0px;
            border-radius:0px;
    background:#f8f8f8;
    cursor:pointer;
    }
    .checkbox-label:before{
    content:'';
    display:block;
    position:absolute;
    z-index:1;
    line-height:34px;
    text-indent:40px;
    height:2rem;
    width:36px;
    /*border-radius*/
    -webkit-border-radius:0%;
       -moz-border-radius:0%;
            border-radius:0%;
    top:0px;
    left:0px;
    right:auto;
    background:white;
    /*box-shadow*/
    -webkit-box-shadow:0 3px 3px rgba(0,0,0,.2),0 0 0 2px #dddddd;
       -moz-box-shadow:0 3px 3px rgba(0,0,0,.2),0 0 0 2px #dddddd;
            box-shadow:0 3px 3px rgba(0,0,0,.2),0 0 0 2px #dddddd;
    }
    .checkbox-label:after{
    content:attr(data-off);
    display:block;
    position:absolute;
    z-index:0;
    top:0;
    left:-300px;
    padding:10px;
    height:100%;
    width:300px;
    text-align:right;
    color:#bfbfbf;
    white-space:nowrap;
    }
   
    .ios-toggle:checked + .checkbox-label:before{
    left:calc(100% - 36px);
    /*box-shadow*/
    -webkit-box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,.3);
       -moz-box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,.3);
            box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,.3);
    }
    .ios-toggle:checked + .checkbox-label:after{
    content:attr(data-on);
    left:60px;
    width:36px;
    }
    
    /* BLUE CHECKBOX */
    
    #checkbox3 + .checkbox-label{
    /*box-shadow*/
    -webkit-box-shadow:inset 0 0 0 0px #2d4d92,0 0 0 2px #dddddd;
       -moz-box-shadow:inset 0 0 0 0px #2d4d92,0 0 0 2px #dddddd;
            box-shadow:inset 0 0 0 0px #2d4d92,0 0 0 2px #dddddd;
    }
    #checkbox3:checked + .checkbox-label{
    /*box-shadow*/
    -webkit-box-shadow:inset 0 0 0 20px #2d4d92,0 0 0 2px #dddddd;
       -moz-box-shadow:inset 0 0 0 20px #2d4d92,0 0 0 2px #dddddd;
            box-shadow:inset 0 0 0 20px #2d4d92,0 0 0 2px #dddddd;
    }
    #checkbox3:checked + .checkbox-label:after{
    color:#dddddd;
    }